<template>
  <view class="record-item">
    <!-- 左侧时间线部分 -->
    <view class="timeline-section">
      <view class="timeline-dot" v-if="showDate"></view>
    </view>

    <!-- 右侧内容部分 -->
    <view class="content-section">
      <view class="date" v-if="showDate">{{ title }}</view>
      <view class="rating-card">
        <slot></slot>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  name: 'RecordItem',
  props: {
    title: {
      type: String,
      default: '',
    },
  },
  computed: {
    showDate() {
      return this.title && this.title.length > 0
    },
  },
}
</script>

